<template>
    <div class="fiction">
        <v-header @parentReload="parentReload"></v-header>
        <ul>
            <li v-for="(item,index) in fictionResultList">
                <router-link :to="'/fiction/chapter/' + item.id" @click="clickFunc">
                    <img v-lazy="item.imgSrc"/>
                </router-link>
                <br/>
                <span class="title" v-html="item.title"></span>
                <p class="center">
                    <span class="author" v-html="item.author"></span> |
                    <span class="updateTime" v-html="item.updateTime"></span> |
                    <span class="status" v-html="item.status"></span>
                </p>
                <p class="content" v-html="item.introduction"></p>
            </li>
        </ul>
    </div>
</template>

<script type="module">
    import axios from "axios";
    import $ from "jquery";
    import Vue from "vue";
    import VueLazyLoad from "vue-lazyload";
    import FictionHeader from "./FictionHeader";

    Vue.use(VueLazyLoad, {
        loading: require("../../assets/images/bg.gif"),
        error: require("../../assets/images/bg.gif")
    });

    export default {
        components: {
            "v-header": FictionHeader
        },
        inject: ['reload'], //注入App里的reload方法
        data() {
            return {
                fictionResultList: null,
                keyword: null,
                pageIndex: 0,
                flag: true
            };
        },
        methods: {
            parentReload() {
                this.reload();
            },
            clickFunc() {
                this.$emit("parentReload");
            }
        },
        created() {
            this.keyword = this.$route.params.keyword;
            axios.get("/fiction/queryByKeyword/?keyword=" + this.keyword + "&offset=0&size=50")
                .then((res) => {
                    let data = res.data;
                    for (let i = 0; i < data.length; i++) {
                        data[i].introduction = data[i].introduction.substring(0, 150) + "...";
                        data[i].id = data[i].href.split("/")[4].split("\.")[0];
                    }
                    this.fictionResultList = res.data;
                });
            let flag = true;
            let pageIndex = 1;
            let _this = this;

            $(window).scroll(function () {
                if ($(document).scrollTop() >= ($(document).height() - $(window).height()) * 0.9) {
                    if (_this.flag) {
                        _this.pageIndex++;
                        flag = false;
                        axios.get("/fiction/queryByKeyword/?keyword=" + _this.keyword + "&offset=" + _this.pageIndex + "&size=50")
                            .then((res) => {
                                let data = res.data;
                                for (let i = 0; i < data.length; i++) {
                                    data[i].introduction = data[i].introduction.substring(0, 150) + "...";
                                    data[i].id = data[i].href.split("/")[4].split("\.")[0];
                                    _this.fictionResultList.push(data[i]);
                                }
                                _this.flag = true;
                            });
                    }
                }
            });
        }
    }
</script>

<style lang="less" scoped>
    * {
        margin: 0;
        padding: 0;
    }

    html, body {
        width: 100%;
        height: 100%;
        overflow: hidden;
    }

    ul, li {
        list-style: none;
    }

    a {
        text-decoration: none;
        color: #000000;
    }

    img {
        vertical-align: top;
    }

    div.fiction {
        width: 100%;
        position: relative;
    }

    div.fiction ul {
        width: 100%;
    }

    div.fiction ul li {
        text-align: center;
        margin: 20px 0;
    }

</style>