<template>
    <div class="cartoonView">
        <v-header @parentReload="parentReload"></v-header>
        <ul>
            <li v-for="(item,index) in textList" v-html="item"></li>
        </ul>
        <ul>
            <li v-for="(item,index) in images">
                <img :src="item"/>
            </li>
        </ul>
    </div>
</template>

<script type="module">
    import axios from "axios";
    import CartoonHeader from "./CartoonHeader";
    import $ from "jquery";
    import Vue from "vue";
    import VueLazyLoad from "vue-lazyload";

    Vue.use(VueLazyLoad, {
        loading: require("../../assets/images/bg.gif"),
        error: require("../../assets/images/bg.gif")
    });

    export default {
        components : {
            "v-header" : CartoonHeader
        },
        inject:['reload'], //注入App里的reload方法
        data(){
            return {
                textList : null,
                chapterList : null,
                images : [],
                chapterIndex : 0,
                index : 1,
                pageIndex : 1,
                flag : true
            };
        },
        methods :{
            parentReload(){
                this.chapterIndex = 0;
                this.index = 1;
                this.pageIndex = 1;
                this.flag = true;
                this.reload();
            },
            clickFunc() {
                this.$emit("parentReload");
            }
        },
        created() {
            let url = this.$route.params.url;
            axios.get("/cartoonContent?url=" + encodeURIComponent(url))
                .then((res)=>{
                    this.textList = res.data.textList;
                    this.chapterList = res.data.chapterList;
                    axios.get("/getChapterToAImage?chapterUrl="+this.chapterList[0].url+"&index=1")
                        .then((res)=>{
                            let imgSrc = res.data.imgSrc;
                            this.images.push(imgSrc);
                        });
                });

            let _this = this;

            $(window).scroll(function() {
                if ($(document).scrollTop() >= ($(document).height() - $(window).height()) * 0.9) {
                    if (_this.flag){
                        _this.pageIndex ++;
                        _this.flag = false;
                        axios.get("/getChapterToAImage?chapterUrl="+_this.chapterList[_this.chapterIndex].url+"&index=" + ++_this.index)
                            .then((res) => {
                                let imgSrc = res.data.imgSrc;
                                if ("https://res.xiaoqinre.com/images/default/cover.png" === imgSrc){
                                    _this.chapterIndex ++;
                                }
                                else{
                                    if (_this.chapterIndex < _this.chapterList.length){
                                        _this.index ++;
                                        _this.images.push(imgSrc);
                                    }
                                }
                                _this.flag = true;
                            });
                    }
                }
            });
        }
    }

    Array.prototype.remove = function(val) {
        var index = this.indexOf(val);
        if (index > -1) {
            this.splice(index, 1);
        }
    };
</script>

<style lang="less" scoped>
    *{
        margin: 0;
        padding: 0;
    }

    html,body{
        width: 100%;
        height: 100%;
        overflow: hidden;
    }

    ul,li{
        list-style: none;
    }

    a{
        text-decoration: none;
        color: #000000;
    }

    img{
        vertical-align: top;
    }

    div.fixed{
        position: fixed;
        width: 100%;
        height: 100%;
        top: 0;
        background-color: rgba(0,0,0,.5);
        display: none;
        z-index: 999999999;
        transition: all 1s;
    }

    div.fixed img{
        transition: all 1s;
    }

    div.cartoonView ul{
        box-shadow: 0.1875rem 0.1875rem 0.1875rem 0.1875rem #333333;
        margin: 1.25rem;
        position: relative;
    }

    div.cartoonView ul li{
        text-align: center;
    }

    div.cartoonView img{
        width: 100%;
    }
</style>