<template>
    <div class="cartoon">
        <v-header @parentReload="parentReload"></v-header>
        <ul>
            <li v-for="(item,index) in cartoonResultList">
                <router-link :to="'/cartoonContentView/' + encodeURIComponent(item.url)" @click="clickFunc">
                    <img v-lazy="item.imgUrl" class="rounded mx-auto d-block"/>
                    <p v-html="item.title"></p>
                    <p v-html="item.jiNumber"></p>
                </router-link>
            </li>
        </ul>
    </div>
</template>

<script type="module">
    import axios from "axios";
    import CartoonHeader from "./CartoonHeader";
    import $ from "jquery";
    import Vue from "vue";
    import VueLazyLoad from "vue-lazyload";

    Vue.use(VueLazyLoad, {
        loading: require("../../assets/images/bg.gif"),
        error: require("../../assets/images/bg.gif")
    });

    export default {
        components : {
            "v-header" : CartoonHeader
        },
        inject:['reload'], //注入App里的reload方法
        data(){
            return {
                cartoonResultList : null,
                prefixUrl : null,
                pageIn : 1,
                classfyName : null,
                pageIndex : 1,
                flag : true
            };
        },
        methods :{
            parentReload(){
                this.reload();
            },
            clickFunc() {
                this.$emit("parentReload");
            }
        },
        created() {
            this.prefixUrl = this.$route.params.prefixUrl;
            this.pageIn = this.$route.params.pageIndex;
            this.classfyName = this.$route.params.classfyName;
            axios.get("/cartoonPages?prefixUrl=%2Flist%2F"+this.prefixUrl+"%2F&pageIndex="+this.pageIn+"&classfyName=" + encodeURIComponent(this.classfyName))
                .then((res)=>{
                    let cartoonResultList = res.data.cartoonPageContents;
                    for (let cartoonResult of cartoonResultList) {
                        let image = new Image(cartoonResult.imgUrl);
                        image.onerror = ((cartoonResult)=>{
                            cartoonResultList.remove(cartoonResult);
                        })(cartoonResult);
                    }
                    this.cartoonResultList = cartoonResultList;
                });

            let _this = this;

            $(window).scroll(function() {
                if ($(document).scrollTop() >= ($(document).height() - $(window).height()) * 0.9) {
                    if (_this.flag){
                        _this.pageIndex ++;
                        _this.flag = false;
                        axios.get("/cartoonPages?prefixUrl=%2Flist%2F"+_this.prefixUrl+"%2F&pageIndex="+_this.pageIndex+"&classfyName=" + encodeURIComponent(_this.classfyName))
                            .then((res) => {
                                let cartoonResultList = res.data.cartoonPageContents;
                                for (let cartoonResult of cartoonResultList) {
                                    let image = new Image(cartoonResult.imgUrl);
                                    image.onerror = ((cartoonResult)=>{
                                        cartoonResultList.remove(cartoonResult);
                                    })(cartoonResult);
                                }
                                for (let i = 0; i < cartoonResultList.length; i++) {
                                    _this.cartoonResultList.push(cartoonResultList[i]);
                                }
                                _this.flag = true;
                            });
                    }
                }
            });
        }
    }

    Array.prototype.remove = function(val) {
        var index = this.indexOf(val);
        if (index > -1) {
            this.splice(index, 1);
        }
    };
</script>

<style lang="less" scoped>
    *{
        margin: 0;
        padding: 0;
    }

    html,body{
        width: 100%;
        height: 100%;
        overflow: hidden;
    }

    ul,li{
        list-style: none;
    }

    a{
        text-decoration: none;
        color: #000000;
    }

    img{
        vertical-align: top;
    }

    div.fixed{
        position: fixed;
        width: 100%;
        height: 100%;
        top: 0;
        background-color: rgba(0,0,0,.5);
        display: none;
        z-index: 999999999;
        transition: all 1s;
    }

    div.fixed img{
        transition: all 1s;
    }

    div.cartoon ul{
        overflow: hidden;
		margin-top: 0.625rem;
    }

    div.cartoon ul li{
        float: left;
        width: 50%;
		padding: 0 0.3125rem;
        position: relative;
    }
	
	div.cartoon ul li:nth-child(odd){
		clear: both;
	}

    div.cartoon ul li img{
        width: 100%;
    }

    div.cartoon ul li p{
        background-color: rgba(0,0,0,.5);
        text-align: center;
        color: #FFFFFF;
    }
</style>