<template>
    <div class="app container-fluid">
        <v-header @parentReload="parentReload"></v-header>
        <div class="list-view">
            <ul class="appUl">
                <li v-for="(item,index) in appResultList">
                    <p>
                        <img v-lazy="item.iconUrl" class="img-fluid rounded">
                        <br>
                        <span class="title" v-html="item.appName.substr(0,10) + '...'"></span>
                        <br>
                        <span class="version" v-html="'版本：&nbsp;&nbsp;' +item.versionName"></span>
                        <br>
                        <span class="size" v-html="(item.fileSize * 1.0 / 1024 / 1024).toFixed(2) + 'MB'"></span>
                    </p>
                    <p>
                        <a :href="item.apkUrl">
                            <img src="../../assets/timg.png">
                        </a>
                    </p>
                </li>
            </ul>
        </div>
    </div>
</template>

<script type="module">
    import axios from "axios";
    import AppHeader from "./AppHeader";
    import $ from "jquery";
    import Vue from "vue";
    import VueLazyLoad from "vue-lazyload";
    import "bootstrap";

    Vue.use(VueLazyLoad, {
        loading: require("../../assets/images/bg.gif"),
        error: require("../../assets/images/bg.gif")
    });

    export default {
        components: {
            "v-header": AppHeader
        },
        inject: ['reload'], //注入App里的reload方法
        data() {
            return {
                appResultList: null,
                categoryId : null,
                pageIndex : 1,
                flag : true
            };
        },
        methods: {
            parentReload() {
                this.categoryId = this.$route.params.categoryId;
                this.reload();
            }
        },
        created() {
            this.categoryId = this.$route.params.categoryId;
            axios.get("/getClassifyApps?categoryId=" + this.categoryId + "&pageSize=60&pageContext=1")
                .then((res)=>{
                    getData(res,_this);
                });

            let _this = this;

            $(window).scroll(function () {
                if ($(document).scrollTop() >= ($(document).height() - $(window).height()) * 0.9) {
                    if (_this.flag) {
                        _this.pageIndex++;
                        _this.flag = false;
                        _this.categoryId = _this.$route.params.categoryId;
                        axios.get("/getClassifyApps?categoryId=" + _this.categoryId + "&pageSize=60&pageContext=" + _this.pageIndex)
                            .then((res) => {
                                let appResultList = res.data.apps;
                                for (let i = 0; i < appResultList.length; i++) {
                                    _this.appResultList.push(appResultList[i]);
                                }
                                _this.flag = true;
                            });
                    }
                }
            });
        }
    }

    function getData(res,_this) {
        if (res.data.apps.length === 0) {
            axios.get("/getClassifyApps?categoryId=" + _this.categoryId + "&pageSize=60&pageContext=1")
                .then((_res)=>{
                    getData(_res,_this);
                });
        } else {
            _this.appResultList = res.data.apps;
        }
    }
</script>

<style lang="less" scoped>
    * {
        margin: 0;
        padding: 0;
    }

    html, body {
        width: 100%;
        height: 100%;
        overflow: hidden;
    }

    ul, li {
        list-style: none;
    }

    a {
        text-decoration: none;
        color: #000000;
    }

    img {
        vertical-align: top;
    }

    div.app {
        width: 100%;
		overflow: hidden;
        position: relative;
    }

    div.app ul {
		width: 100%;
        overflow: hidden;
        position: relative;
        top: -1rem;
    }

    div.app ul li {
        float: left;
        width: 47%;
        position: relative;
		padding: 0.3125rem;
    }

    div.app ul li img {
        width: 100%;
        vertical-align: middle;
    }

    div.list-view ul.appUl {
        width: 100%;
        overflow: hidden;
		margin-top: 1.25rem;
    }

    div.list-view ul.appUl li {
        display: inline-block;
        position: relative;
        width: 50%;
    }

    div.list-view ul.appUl li p {
        width: 100%;
        text-align: center;
    }

    div.list-view ul.appUl li:hover p img {
        transform: scale(1.5);
    }

    div.list-view ul.appUl li p span {
        padding: 0.1875rem;
    }

    div.list-view ul.appUl li p:nth-of-type(2) {
        position: absolute;
        right: 27%;
        bottom: 28%;
        width: 2rem;
        height: 2rem;
    }

    div.list-view ul.appUl li p:nth-of-type(2) img {
        width: 100%;
    }

    div.list-view ul.appUl li p img {
        width: 100%;
        transition: all 0.5s;
    }
</style>