<template>
    <div class="show">
        <h1>{{name}}</h1>
        <iframe
                allowfullscreen="true"
                webkitallowfullscreen="true"
                mozallowfullscreen="true"></iframe>
    </div>
</template>

<script type="module">
    import $ from "jquery";
    export default {
        data(){
            return {
                name : null
            };
        },
        methods: {
        },
        mounted() {
            let url = this.$route.params.playUrl;
            this.name = this.$route.params.name;
            $("iframe").attr("src","https://api.smq1.com/?url=" + url);
        }
    };
</script>

<style lang="less" scoped>
    *{
        margin: 0;
        padding: 0;
    }

    ul,li{
        list-style: none;
    }

    a{
        text-decoration: none;
        color: #000000;
    }

    img{
        vertical-align: top;
    }

    div.show{
        width: 100%;
        position: relative;
    }

    h1{
        text-align: center;
    }

    iframe{
        width: 100%;
		height: 50rem;
        outline: none;
        background-color: #000000;
    }
</style>