import Vue from "vue";
import VueRouter from "vue-router";
import Tv from "../views/tv/Tv";
import Home from "../components/Home";
import ShowTv from "../views/tv/ShowTv";
import SearchTv from "../views/tv/SearchTv";
import Movie from "../views/movie/Movie";
import ShowMoving from "../views/movie/ShowMoving";
import SearchMovie from "../views/movie/SearchMovie";
import Images from "../views/inage/Images";
import App from "../views/app/App";
import AppSearchResult from "../views/app/AppSearchResult";
import Music from "../views/music/Music";
import CartoonIndex from "../views/cartoon/CartoonIndex";
import Cartoon from "../views/cartoon/Cartoon";
import CartoonView from "../views/cartoon/CartoonView";
import CartoonSearchResult from "../views/cartoon/CartoonSearchResult";
import Fiction from "../views/fiction/Fiction";
import FictionView from "../views/fiction/FictionView";
import FictionSearchResult from "../views/fiction/FictionSearchResult";


Vue.use(VueRouter);

const routes = [
    {
        path: "/",
        name: "Home",
        component: Home
    },
    {
        path: "/tvResult/:pageIndex",
        name: "tv",
        component: Tv
    },
    {
        path: "/showTv/:url/:platform/:aid/:tvName",
        name: "showTv",
        component: ShowTv
    },
    {
        path: "/tvSearchResult/:keyword",
        name: "tvSearchResult",
        component: SearchTv
    },
    {
        path: "/movieResult/:pageIndex",
        name: "movie",
        component: Movie
    },
    {
        path: "/playMovieWithThreePart/:playUrl/:name",
        name: "showMoving",
        component: ShowMoving
    },
    {
        path: "/movieSearchResult/:keyword",
        name: "movieSearchResult",
        component: SearchMovie
    },
    {
        path: "/imageResult/:keyword/:pageIndex/:pageSize",
        name: "image",
        component: Images
    },
    {
        path: "/appResult/:categoryId/:pageSize/:pageContext",
        name: "app",
        component: App
    },
    {
        path: "/appSearchResultJson/:keyword",
        name: "appSearchResult",
        component: AppSearchResult
    },
    {
        path: "/musicResult/:keyword/:pageIndex/:pageSize",
        name: "musicResult",
        component: Music
    },
    {
        path: "/cartoonResult/:prefixUrl/:pageIndex/:classfyName",
        name: "cartoon",
        component: CartoonIndex
    },
    {
        path: "/cartoonResult/list/:prefixUrl/:pageIndex/:classfyName",
        name: "cartoonResult",
        component: Cartoon
    },
    {
        path: "/cartoonContentView/:url",
        name: "cartoonContent",
        component: CartoonView
    },
    {
        path: "/cartoonSearchResult/:keyword",
        name: "cartoonSearchResult",
        component: CartoonSearchResult
    },
    {
        path: "/fiction/fiction",
        name: "fiction",
        component: Fiction
    },
    {
        path: "/fiction/chapter/:id",
        name: "chapter",
        component: FictionView
    },
    {
        path: "/fiction/fictionSearchResult/:keyword",
        name: "fictionSearchResult",
        component: FictionSearchResult
    }
];

const router = new VueRouter({
    //mode: "abstract",
    base: process.env.BASE_URL,
    routes
});

export default router;
