<template>
    <div class="app">
        <v-header @parentReload="parentReload"></v-header>
        <div class="list-view">
            <ul class="appUl">
                <li v-for="(item,index) in appResultList">
                    <p>
                        <img :src="item.iconUrl">
                        <br>
                        <span class="title" v-html="item.appName"></span>
                        <br>
                        <span class="version" v-html="'版本：&nbsp;&nbsp;' +item.versionName"></span>
                        <br>
                        <span class="size" v-html="(item.fileSize * 1.0 / 1024 / 1024).toFixed(2) + 'MB'"></span>
                    </p>
                    <p>
                        <a :href="item.apkUrl">
                            <img src="../../assets/timg.png">
                        </a>
                    </p>
                </li>
            </ul>
        </div>
    </div>
</template>

<script type="module">
    import axios from "axios";
    import AppHeader from "./AppHeader";

    export default {
        components: {
            "v-header": AppHeader
        },
        inject: ['reload'], //注入App里的reload方法
        data() {
            return {
                appResultList: null
            };
        },
        methods: {
            parentReload() {
                this.reload();
            }
        },
        created() {
            let keyword = this.$route.params.keyword;
            axios.get("/appSearchResultJson?keyword=" + encodeURIComponent(keyword))
                .then((res) => {
                    this.appResultList = res.data;
                });
        }
    }
</script>

<style lang="less" scoped>
    * {
        margin: 0;
        padding: 0;
    }

    html, body {
        width: 100%;
        height: 100%;
        overflow: hidden;
    }

    ul, li {
        list-style: none;
    }

    a {
        text-decoration: none;
        color: #000000;
    }

    img {
        vertical-align: top;
    }

    div.app {
        width: 100%;
        position: relative;
    }

    div.app ul {
        width: 48%;
        float: left;
        overflow: hidden;
        position: relative;
        top: -1rem;
    }

    div.app ul li {
        display: block;
        width: 100%;
        position: relative;
    }

    div.app ul li img {
        width: 100%;
        vertical-align: middle;
    }

    div.list-view ul.appUl{
        width: 100%;
        margin: 1.625rem 0;
        overflow: hidden;
    }

    div.list-view ul.appUl li {
        float: left;
        position: relative;
        width: 48%;
        height: 14.375rem;
    }

    div.list-view ul.appUl li p{
        width: 12.5rem;
        text-align: center;
        padding: 0.1875rem;
    }

    div.list-view ul.appUl li:hover p img{
        transform: scale(1.5);
    }

    div.list-view ul.appUl li p span{
        padding: 0.1875rem;
    }

    div.list-view ul.appUl li p:nth-of-type(2){
        position: absolute;
        left: 8.3125rem;
        top: 6.8125rem;
        width: 2rem;
        height: 2rem;
    }

    div.list-view ul.appUl li p:nth-of-type(2) img{
        width: 100%;
    }

    div.list-view ul.appUl li p img{
        width: 70%;
        transition: all 0.5s;
    }
</style>