<template>
    <div class="movie">
        <v-header></v-header>
        <ul>
            <li v-for="(item,index) in movieResultList">
                <router-link :to="'/playMovieWithThreePart/' + encodeURIComponent(item.href) + '/' + item.title">
                    <img v-lazy="item.imgSrc" class="rounded mx-auto"/>
                </router-link>
                <span class="jiNumber">{{item.jiNumber}}</span>
                <p v-html="item.title"></p>
                <p v-html="item.subTitle"></p>
            </li>
        </ul>
    </div>
</template>

<script type="module">
    import axios from "axios";
    import MovieHeader from "./MovieHeader";
    import Vue from "vue";
    import VueLazyLoad from "vue-lazyload";

    Vue.use(VueLazyLoad, {
        loading: require("../../assets/images/bg.gif"),
        error: require("../../assets/images/bg.gif")
    });

    export default {
        components : {
          "v-header" : MovieHeader
        },
        data(){
           return {
               movieResultList : null
           };
        },
        methods :{

        },
        created() {
            axios.get("/moviePlayResult?pageIndex=1")
                .then((res)=>{
                    for (let item of res.data.movieList){
                        item.href = item.href.split("=")[1].split("&")[0];
                    }
                    this.movieResultList = res.data.movieList;
                });
        }
    }
</script>

<style lang="less" scoped>
    *{
        margin: 0;
        padding: 0;
    }

    ul,li{
        list-style: none;
    }

    a{
        text-decoration: none;
        color: #000000;
    }

    img{
        vertical-align: top;
    }

    div.movie{
        width: 100%;
        position: relative;
    }

    div.movie ul {
        width: 100%;
        overflow: hidden;
        position: relative;
        top: -1rem;
    }

    div.movie ul li{
        float: left;
        width: 48%;
        position: relative;
        margin: 0.125rem;
    }
	
	div.movie ul li:nth-child(odd){
		clear: both;
	}

    div.movie ul li img{
        width: 100%;
    }

    div.movie ul li p{
        text-align: center;
        height: 1.875rem;
        line-height: 1.875rem;
        margin: 0;
        position: relative;
    }

    div.movie ul li p:nth-of-type(2){
        font-size: 0.75rem;
    }

    div.movie ul li span.jiNumber{
        position: relative;
        right: -6.3125rem;
        bottom: 1.5625rem;
        color: #FFFFFF;
    }
</style>