<template>
    <div>
        <div class="oneLine">
            <a title="祥龙检索，搜电视剧"><img src="../../assets/images/tv/homelogo.png"/></a>
        </div>
        <p class="searchTag">可以搜索电视剧名称或者演员名字等</p>
        <div class="searchArea">
            <p><input type="text" placeholder="可以搜索电视剧名称或者演员名字等" v-model="keyword"/></p>
            <router-link :to="'/tvSearchResult/' + keyword" class="searchButton btn btn-primary" @click.native="clickFunc">千度电视剧</router-link>
        </div>

        <div id="fixed_top">
            <div class="searchArea">
                <p><input type="text" title="可以搜索电视剧名称或者演员名字等" v-model="keyword"/></p>
                <router-link :to="'/tvSearchResult/' + keyword" class="searchButton btn btn-primary" @click.native="clickFunc">千度电视剧</router-link>
            </div>
        </div>

        <div id="hrefs">
            <ul>
                <li v-for="(item,index) in hrefs">
                    <router-link :to="item[1]">{{item[0]}}</router-link>
                </li>
            </ul>
        </div>
    </div>
</template>


<script type="module">
    import $ from "jquery";
    $(function () {
        $(document).scroll(function () {
            if ($(this).scrollTop() >= 180){
                $("div#fixed_top").css("display","block");
            }
            if ($(this).scrollTop() < 140){
                $("div#fixed_top").css("display","none");
            }
        });
    });

    export default {
        data(){
            return {
                keyword : "",
                hrefs : [
                    ["回首页", "/"],
                    ["电视剧", "/tvResult/1"],
                    ["电影", "/movieResult/1"],
                    ["图片", "/imageResult/美女/1/60"],
                    ["app", "/appResult/-10/60/1"],
                    ["音乐", "/musicResult/周杰伦/1/60"],
                    ["动漫", "/cartoonResult/list%2F/1/全部"],
                    ["爱小说","/fiction/fiction"]
                ]
            };
        },
        methods : {
            clickFunc(){
                this.$emit("parentReload");
            }
        },
        created (){
          this.keyword = this.$parent.$route.params.keyword;
        },
        watch :{
            keyword(){

            }
        }
    }
</script>

