<template>
    <div class="image">
        <v-header @parentReload="parentReload"></v-header>
        <ul>
            <li v-for="(item,index) in imageResultFirstList" @click="imageClickFunc">
                <img v-lazy="item.middleURL"/>
                <p v-html="item.fromPageTitle"></p>
            </li>
        </ul>
        <ul>
            <li v-for="(item,index) in imageResultSecondList" @click="imageClickFunc">
                <img v-lazy="item.middleURL"/>
                <p v-html="item.fromPageTitle"></p>
            </li>
        </ul>
        <div class="fixed" @click="fixedClickFunc" ref="fixedDiv">
            <img />
        </div>
    </div>
</template>

<script type="module">
    import axios from "axios";
    import ImageHeader from "./ImageHeader";
    import $ from "jquery";
    import Vue from "vue";
    import VueLazyLoad from "vue-lazyload";

    Vue.use(VueLazyLoad, {
        loading: require("../../assets/images/bg.gif"),
        error: require("../../assets/images/bg.gif")
    });

    export default {
        components : {
            "v-header" : ImageHeader
        },
        inject:['reload'], //注入App里的reload方法
        data(){
            return {
                imageResultFirstList : [],
                imageResultSecondList : []
            };
        },
        methods :{
            imageClickFunc (event){
                let src = $(event.target).attr("src");
                let fixedDiv = $("div.fixed");
                fixedDiv.find("img").attr("src",src);
                fixedDiv.css("display","block");
                fixedDiv.find("img").css("display","block");
            },
            fixedClickFunc (){
                $(this.$refs.fixedDiv).css("display","none");
            },
            parentReload(){
                this.reload();
            }
        },
        created() {
            let keyword = this.$route.params.keyword;
            axios.get("/getAllMatchingImages?keyword="+encodeURIComponent(keyword)+"&pageIndex=1&pageSize=60")
                .then((res)=>{
                    let length = res.data.length;
                    let firStLength = length/2;
                    for (let i=0;i<firStLength;i++){
                        this.imageResultFirstList.push(res.data[i]);
                    }
                    for (let i=firStLength;i<length;i++){
                        this.imageResultSecondList.push(res.data[i]);
                    }
                });
            let flag = true;
            let pageIndex = 1;
            let _this = this;

            $(window).scroll(function() {
                if ($(document).scrollTop() >= ($(document).height() - $(window).height()) * 0.9) {
                    if (flag){
                        pageIndex ++;
                        flag = false;
                        axios.get("/getAllMatchingImages?keyword="+encodeURIComponent(keyword)+"&pageIndex=" + pageIndex + "&pageSize=60")
                            .then((res) => {
                                let length = res.data.length;
                                let firStLength = length/2;
                                for (let i=0;i<firStLength;i++){
                                    _this.imageResultFirstList.push(res.data[i]);
                                }
                                for (let i=firStLength;i<length;i++){
                                    _this.imageResultSecondList.push(res.data[i]);
                                }
                                flag = true;
                            });
                    }
                }
            });
        }
    }
</script>

<style lang="less" scoped>
    *{
        margin: 0;
        padding: 0;
    }

    html,body{
        width: 100%;
        height: 100%;
        overflow: hidden;
    }

    ul,li{
        list-style: none;
    }

    a{
        text-decoration: none;
        color: #000000;
    }

    img{
        vertical-align: top;
    }

    div.image{
        width: 100%;
        position: relative;
    }

    div.image ul {
        width: 48%;
        float: left;
        overflow: hidden;
        position: relative;
        top: -1rem;
    }

    div.image ul li{
        display: block;
        width: 100%;
        position: relative;
    }

    div.image ul li img{
        width: 100%;
        vertical-align: middle;
    }

    p{
        position: relative;
        left: 0.5rem;
        top: -1.8125rem;
        color: #FFFFFF;
        font-size: 0.75rem;
    }

    div.fixed{
        position: fixed;
        width: 100%;
        height: 100%;
        top: 0;
        background-color: rgba(0,0,0,.5);
        display: none;
        z-index: 999999999;
        transition: all 1s;
    }

    div.fixed img{
        transition: all 1s;
    }
</style>