<template>
    <div class="music">
        <v-header @parentReload="parentReload"></v-header>
        <ul>
            <li v-for="(item,index) in musicResultList">
                <img v-lazy="item.picUrl"/>
                <span v-html="item.album"></span>
                <span v-html="item.singer"></span>
                <span @click="playClickFunc" :src="item.musicUrl" class="musicPlay">播放</span>
            </li>
        </ul>

        <audio controls autoplay></audio>
    </div>
</template>

<script type="module">
    import axios from "axios";
    import MusicHeader from "./MusicHeader";
    import Vue from "vue";
    import VueLazyLoad from "vue-lazyload";
    import $ from "jquery";

    Vue.use(VueLazyLoad, {
        loading: require("../../assets/images/bg.gif"),
        error: require("../../assets/images/bg.gif")
    });

    export default {
        components : {
            "v-header" : MusicHeader
        },
        inject: ['reload'], //注入App里的reload方法
        data(){
            return {
                musicResultList : null
            };
        },
        methods :{
            parentReload() {
                this.reload();
            },
            playClickFunc(event) {
                let spanButton = $(event.target);
                let spanButtons = $("span.musicPlay");
                $("audio").attr("src",spanButton.attr("src"));
                spanButtons.parent().css("background-color","#FFFFFF");
                spanButtons.text("播放");
                spanButton.text("播放中......");
                spanButton.parent().css("background-color","#CCCCCC");
            }
        },
        created() {
            let keyword = this.$route.params.keyword;
            axios.get("/getAllMatchingMusics?keyword=" + keyword + "&pageIndex=1&pageSize=60")
                .then((res)=>{
                   this.musicResultList = res.data;
                });
            let flag = true;
            let pageIndex = 1;
            let _this = this;

            $(window).scroll(function () {
                if ($(document).scrollTop() >= ($(document).height() - $(window).height()) * 0.9) {
                    if (flag) {
                        pageIndex++;
                        flag = false;
                        axios.get("/getAllMatchingMusics?keyword=" + keyword + "&pageIndex="+pageIndex+"&pageSize=60")
                            .then((res) => {
                                let musicResultList = res.data;
                                for (let i=0;i<musicResultList.length;i++){
                                    _this.musicResultList.push(musicResultList[i]);
                                }
                                flag = true;
                            });
                    }
                }
            });
        }
    }
</script>

<style lang="less" scoped>
    *{
        margin: 0;
        padding: 0;
    }

    ul,li{
        list-style: none;
    }

    a{
        text-decoration: none;
        color: #000000;
    }

    img{
        vertical-align: top;
    }

    div.music{
        width: 100%;
        position: relative;
    }

    div.music ul {
        width: 100%;
        overflow: hidden;
        position: relative;
        top: -1rem;
    }

    div.music ul li{
        display: flex;
        height: 7.5rem;
        line-height: 6.375rem;
        margin: 0.125rem;
        position: relative;
        border-bottom: 0.0625rem solid #CCCCCC;
        transition: all 1s;
    }

    div.music ul li img{
        width: 6.375rem;
        flex: 1;
    }

    div.music ul li span{
        display: inline-block;
        height: 100%;
        flex: 1;
        text-align: center;
    }

    audio{
        display: none;
    }
</style>