<template>
    <div class="cartoonSearch">
        <v-header @parentReload="parentReload"></v-header>
        <ul>
            <li v-for="(item,index) in cartoonSearchResultList">
                <router-link :to="'/cartoonContentView/' + encodeURIComponent(item.url)" @click="clickFunc">
                    <img v-lazy="item.imgSrc" class="rounded mx-auto d-block"/>
                    <p v-html="item.title"></p>
                    <p v-html="item.updateTime"></p>
                </router-link>
            </li>
        </ul>
    </div>
</template>

<script type="module">
    import axios from "axios";
    import CartoonHeader from "./CartoonHeader";
    import $ from "jquery";
    import Vue from "vue";
    import VueLazyLoad from "vue-lazyload";

    Vue.use(VueLazyLoad, {
        loading: require("../../assets/images/bg.gif"),
        error: require("../../assets/images/bg.gif")
    });

    export default {
        components : {
            "v-header" : CartoonHeader
        },
        inject:['reload'], //注入App里的reload方法
        data(){
            return {
                cartoonSearchResultList : null
            };
        },
        methods :{
            parentReload(){
                this.reload();
            },
            clickFunc() {
                this.$emit("parentReload");
            }
        },
        created() {
            let keyword = this.$route.params.keyword;
            axios.get("/searchCartoon/" + keyword)
                .then((res)=>{
                    this.cartoonSearchResultList = res.data;
                });
        }
    }

    Array.prototype.remove = function(val) {
        let index = this.indexOf(val);
        if (index > -1) {
            this.splice(index, 1);
        }
    };
</script>

<style lang="less" scoped>
    *{
        margin: 0;
        padding: 0;
    }

    html,body{
        width: 100%;
        height: 100%;
        overflow: hidden;
    }

    ul,li{
        list-style: none;
    }

    a{
        text-decoration: none;
        color: #000000;
    }

    img{
        vertical-align: top;
    }

    div.fixed{
        position: fixed;
        width: 100%;
        height: 100%;
        top: 0;
        background-color: rgba(0,0,0,.5);
        display: none;
        z-index: 999999999;
        transition: all 1s;
    }

    div.fixed img{
        transition: all 1s;
    }

    div.cartoonSearch ul{
        overflow: hidden;
    }

    div.cartoonSearch ul li{
        float: left;
        width: 50%;
        padding: 0 0.1875rem;
        position: relative;
    }
	
	div.cartoonSearch ul li:nth-child(odd){
		clear: both;
	}

    div.cartoonSearch ul li img{
        width: 100%;
    }

    div.cartoonSearch ul li p{
        background-color: rgba(0,0,0,.5);
        text-align: center;
        color: #FFFFFF;
    }
</style>