<template>
    <div class="fiction">
        <v-header @parentReload="parentReload"></v-header>
        <ul>
            <li v-for="(item,index) in chapterViewList">
                <p class="title" v-html="item.title"></p>
                <p class="author" v-html="'作者：' + item.author + '|字数：' + item.wordCount + '更新时间：' + item.updateTime"></p>
                <div v-html="item.content"></div>
            </li>
        </ul>
    </div>
</template>

<script type="module">
    import axios from "axios";
    import $ from "jquery";
    import FictionHeader from "./FictionHeader";

    export default {
        components: {
            "v-header": FictionHeader
        },
        inject: ['reload'], //注入App里的reload方法
        data() {
            return {
                chapterList: null,
                chapterIndex : 0,
                chapterViewList : []
            };
        },
        methods: {
            parentReload() {
                this.chapterIndex = 0;
                this.chapterViewList = [];
                this.reload();
            },
            clickFunc() {
                this.$emit("parentReload");
            }
        },
        created() {
            let id = this.$route.params.id;
            axios.get("/fiction/showChapter/" + id)
                .then((res) => {
                    this.chapterList = res.data[0].chapters;
                    axios.get("/fiction/getChapterCatalog/"+id+"/" + this.chapterList[0].chapterUrl.split("/")[5].split("\.")[0])
                        .then((res)=>{
                             this.chapterViewList.push(res.data);
                        });
                });
            let flag = true;
            let pageIndex = 1;
            let _this = this;

            $(window).scroll(function () {
                if ($(document).scrollTop() >= ($(document).height() - $(window).height()) * 0.9) {
                    if (flag) {
                        pageIndex++;
                        flag = false;
                        axios.get("/fiction/getChapterCatalog/"+id+"/" + _this.chapterList[++_this.chapterIndex].chapterUrl.split("/")[5].split("\.")[0])
                            .then((res) => {
                                _this.chapterViewList.push(res.data);
                                flag = true;
                            });
                    }
                }
            });
        }
    }
</script>

<style lang="less" scoped>
    * {
        margin: 0;
        padding: 0;
    }

    html, body {
        width: 100%;
        height: 100%;
        overflow: hidden;
    }

    ul, li {
        list-style: none;
    }

    a {
        text-decoration: none;
        color: #000000;
    }

    img {
        vertical-align: top;
    }

    div.fiction {
        width: 100%;
        position: relative;
    }

    div.fiction ul {
        width: 100%;
    }

    div.fiction ul li {
        text-align: left;
        padding: 20px 0;
        background-color: #faeed7;
    }

    div.fiction ul li p.title,
    div.fiction ul li p.author{
        text-align: center;
        font-weight: bolder;
    }

</style>